.section {
  padding: 5rem 0;
}

.sectionTitle {
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 3rem;
  color: var(--text-main);
  position: relative;
  display: inline-block;
  left: 50%;
  transform: translateX(-50%);
}

.sectionTitle::after {
  content: '';
  display: block;
  width: 60px;
  height: 4px;
  background-color: var(--accent);
  margin: 10px auto 0;
  border-radius: 2px;
}

.sectionHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
}

.sectionHeader .sectionTitle {
  left: auto;
  transform: none;
  margin-bottom: 0;
}

/* Features */
.features {
  padding: 3rem 0;
  background-color: white;
  border-bottom: 1px solid var(--border);
}

.featuresGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
  text-align: center;
}

.feature {
  padding: 1rem;
}

.featureIcon {
  color: var(--primary);
  margin-bottom: 1rem;
  background-color: #fff0f0;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}

.feature h3 {
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
}

.feature p {
  color: var(--text-muted);
  font-size: 0.9rem;
}

/* Categories */
.categoryGrid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
}

.categoryCard {
  border-radius: var(--radius);
  padding: 2rem;
  text-align: center;
  transition: transform var(--transition-medium);
  cursor: pointer;
  box-shadow: var(--shadow-sm);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 200px;
}

.categoryCard:hover {
  transform: translateY(-10px);
  box-shadow: var(--shadow-md);
}

.catIcon {
  font-size: 3rem;
  margin-bottom: 1rem;
  display: block;
}

.catName {
  font-family: var(--font-nunito);
  color: rgba(0, 0, 0, 0.7);
  background-color: rgba(255, 255, 255, 0.5);
  padding: 0.25rem 1rem;
  border-radius: 20px;
  display: inline-block;
}

/* Products */
.productGrid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 2rem;
}

@media (max-width: 600px) {
  .section {
    padding: 3rem 0;
  }

  .sectionTitle {
    font-size: 1.8rem;
    margin-bottom: 2rem;
  }

  .productGrid {
    grid-template-columns: repeat(2, 1fr);
    /* Force 2 columns for "app-like" feel */
    gap: 1rem;
  }

  .categoryGrid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }

  .featuresGrid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .ctaCard {
    padding: 2rem 1rem;
  }

  .ctaCard h2 {
    font-size: 1.8rem;
  }
}

/* Very small screens (iPhone 5/SE era) */
@media (max-width: 370px) {
  .productGrid {
    grid-template-columns: 1fr;
    /* Back to 1 column for very narrow screens */
  }
}

/* CTA */
.ctaSection {
  padding: 6rem 0;
}

.ctaCard {
  background: radial-gradient(circle at center, #6c5ce7 0%, #a29bfe 100%);
  border-radius: 30px;
  padding: 4rem 2rem;
  text-align: center;
  color: white;
  box-shadow: var(--shadow-lg);
  position: relative;
  overflow: hidden;
}

.ctaCard h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.ctaCard p {
  font-size: 1.2rem;
  margin-bottom: 2.5rem;
  opacity: 0.9;
}

.inputGroup {
  display: flex;
  max-width: 500px;
  margin: 0 auto;
  gap: 1rem;
}

.inputGroup input {
  flex: 1;
  padding: 1rem 1.5rem;
  border-radius: 50px;
  border: none;
  font-size: 1rem;
  outline: none;
}

@media (max-width: 768px) {
  .categoryGrid {
    grid-template-columns: repeat(2, 1fr);
  }

  .inputGroup {
    flex-direction: column;
  }
}
